.search{
    width: 100%;
    padding: 3rem 0;
}
.search .container{
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 1rem;
    padding: 0 1rem;
}
.search-col-2{
    display: grid;
    grid-template-columns: 1fr 1fr;
        padding: 2rem 0;
        justify-content: center;
        align-items: center;
}
.search-col-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 2rem 0;
    justify-content: center;
    align-items: center;
}

.search .box {
    display: flex;
    max-width: 310px;   
    align-items: center;
    margin: auto;
    text-align: center;
}
.promo {
    width: 100%;
    margin: .4rem 0;
    margin-bottom: 4rem;
    border: 1px solid #333;
    text-align: center;
}

.promo h4 {
    font-size: 1.6rem;
    color: var(--primary-dark);
}

.promo .timer {
    padding: 1rem 0;
    font-size: 1.2rem;
}

.promo .offers {
    background: #333;
    color: #f5f5f5;
    padding: .4rem 0;
}

.search form {
    display: flex;
    flex-direction: column;
}

.input-wrap {
    margin: .4rem;
    display: flex;
    flex-direction: column;
}

.search input, select {
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    outline: none;
}

.search .date {
    display: flex;
}

.search button {
    margin: .4rem;
}


@media screen and (max-width: 940px) {
    .search .container {
        grid-template-columns: 2fr;
    }

    .search-col-2 {
        grid-template-columns: 1fr ;
        margin-bottom: .5rem;
    }
    .search .box {
        margin-bottom: .1rem;
        text-align: start;
    }

    .search .right {
        max-width: 100%;
    }

    .search form {
        width: 100%;
    }

    .search .date {
        flex-direction: column;
    }
}