@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;600;900&display=swap');



:root{
  --primary-dark:#5651e5;
  --primary-light:#709dff ;
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  font-family: 'Poppins', sans-serif;
}


ul{
  list-style-type: none;
}
a{
  text-decoration: none;
}
h1{
  font-size: 3.5rem;
}
h2{
  font-size: 2rem;
}
.container{
  max-width: 1240px;
  margin: auto;
  padding: 0 1rem;
}
button{
  background-image: linear-gradient(45deg,var(--primary-dark)0% ,var(--primary-light)100%);
  border: 1px solid var(--primary-dark);
  border-radius: 8px;
  color: #fff;
  padding: .7rem,1.5rem;
  font-family: 'Poppins',sans-serif;
}
@media screen and(max-width:940x){
  h1{
    font-size: 2.5rem;
  }
  h2{
    font-size: 1.5rem;
  }
}